import React from 'react';

export default (OriginalComponent) => class WrappedComponent extends React.Component {
	state = {
		isOpen: false
	}

	render(){
		return (
			//<OriginalComponent {...this.props} isOpen = {this.state.isOpen} toggleOpen = {this.toggleOpen}/>
			<OriginalComponent {...this.props} {...this.state} toggleOpen = {this.toggleOpen}/>
		);
	}

	toggleOpen = (ev) => {
		ev && ev.preventDefault && ev.preventDefault()
		this.setState({
			isOpen: !this.state.isOpen
		});
	}
}