import React from 'react';
import PropTypes from 'prop-types';
import CommentList from './CommentList';
import { CSSTransitionGroup } from 'react-transition-group';
import './article.css';
//import toggleOpen from '../decorators/toggleOpen'; - with decorators

class Article extends React.Component {
	static propTypes = {
		article: PropTypes.shape({
			id: PropTypes.number,
			title: PropTypes.string,
			text: PropTypes.string,
			comments: PropTypes.array
		}),
		isOpen: PropTypes.bool, // !!!!! forget
		toggleOpen: PropTypes.func // !!!!! forget
	}
	/*constructor(props){
		super(props);

		this.state = {
			isOpen: false
		}
	}*/

	render(){
		const article = this.props.article;
		return (
			<div>
				<h3>{article.title}</h3>
				<button onClick={() => this.props.toggleOpen()}>
					{this.props.isOpen ? 'close' : 'open'}
				</button>
				
					{this.getBody()}
				
			</div>
		);
	}

	getBody(){
		if (!this.props.isOpen) return null;
		const article = this.props.article;
		return (
			<section>
				{article.text}
				<CommentList comments = {article.comments} ref = {this.setCommentsRef}/>
			</section>
		);
	}

	// Example ref
	setCommentsRef = (ref) => {
		console.log('---', ref);
		//ref.focus(); //- method of CommentList
	}

	focus(){
		console.log('focus');
	}

	/*
	toggleOpen(){
		this.setState({
			isOpen: !this.state.isOpen
		});
	}*/
}

//export default toggleOpen(Article) - with decorators
export default Article