import React from 'react';
import PropTypes from 'prop-types';
import Comment from './Comment';
import toggleOpen from '../decorators/toggleOpen' //- with decorators

class CommentList extends React.Component{
	static propTypes = {
		comments: PropTypes.array,
		isOpen: PropTypes.bool, // !!!!! forgot
		toggleOpen: PropTypes.func // !!!!! forgot
	}

	static defaultProps = {
		comments: []
	}

	/*constructor(props){
		super(props);
		
		this.state = {
			isOpen: false
		}
	}*/

	render(){
		const textButton = this.props.isOpen ? 'hide comments' : 'show comments';
		return (
			<div>
				<button onClick = {() => this.props.toggleOpen()}>{textButton}</button>
				{this.getBody()}
			</div>
		)
	}

	/*toggleOpen(){
		this.setState({
			isOpen: !this.state.isOpen
		});
	}*/

	getBody(){
		const comments = this.props.comments;
		if (comments.length === 0) return null;
		if (!this.props.isOpen) return null;
		const commentElements = comments.map((comment) => <li key={comment.id}><Comment comment = {comment}/></li>);
		return (
			<ul>
				{commentElements}
			</ul>
		)
	}
}

export default toggleOpen(CommentList) //- with decorators